//imported library/ies
import { Outlet, Link } from "react-router-dom";



const Navbar = () =>{   
    return(
        <>
        {/*Navigations bar structure*/}
            <nav className="navbar navbar-expand-lg navbar-light bg-white sticky-top border-bottom">
                <div className="container-fluid">
                    
                    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                    </button>
                <div className="collapse navbar-collapse" id="navbarSupportedContent">
                    <ul className="navbar-nav mx-auto">
                        <li className="nav-item px-2">
                            <Link to = "/reactactivity/Home" className="nav-link "  tabindex="-1" >Home</Link>
                        </li>
                        <li className="nav-item px-2">
                            <Link to = "/reactactivity/PageOne" className="nav-link "  tabindex="-1" >Page Two</Link>
                        </li>
                        <li className="nav-item mx-2">
                            <Link to = "/reactactivity/PageTwo" className="nav-link "  tabindex="-1" >Page Three</Link>
                        </li>
                    
                     
                  
                    </ul>
                    </div>
                </div>
            </nav>
            
            <Outlet />
        </>

    );
}
export default Navbar;