import '../styling/style.css';

const picThree = require('../images/logo-2.png');

const Home = ()=>{
    return(
        <>
        {/*jumbotron*/}
        <div className="jumbotron jumbotron-fluid bg-light text-dark p-5 mt-5 ">
            <div className="container ">
                <div className='row '>
                    <div className='col-lg-7 px-5  text-center' style={{paddingTop:'7.6%'}}>
                        <h1 className="text-start display-4 py-2">Welcome to my Page</h1>
                        <p className="lead text-start">I am Arnold an aspiring Full Stack Developer<br/>How can I help you ?</p>
                        <button className='btn btn-dark float-start'>Get to Know Me</button>
                    </div>
                    
                <div className='col-lg-5 mb-5'>
                    
                    <div className='mx-auto  rounded-circle'>
                        <img src = {picThree} className = " img-fluid " alt = "logo"></img>
                    </div>
                    
                </div>
                </div>
            </div>
        </div>
        </>

    );
}
export default Home;