import * as React from 'react';
import TextField from '@mui/material/TextField';
import Autocomplete from '@mui/material/Autocomplete';
/*List of items in combobox*/
const services = [
    { label: 'Web Designing', rate: 0.00 },
    { label: 'Tech Consultancy', rate: 5.00 },
    { label: 'Application Development', rate: 4.00 },
    { label: 'Project Management', rate: 3.00 },
    { label: 'Back-end Development', rate: 20.00 },
 
   
  ];
  

export default function ComboBox() {
  return (
      <>
        {/*Avail service section with autocomplete combobox*/}
        <div className='container'>
          
          <div className='display-6 mb-5'>Avail Service</div>
          <div className='row'>
            <div className='col-lg-12'>
              
              <div className="mb-3">
                <label for="exampleFormControlInput1" class="form-label">Email address</label>
                <input type="email" class="form-control" id="exampleFormControlInput1" placeholder="person_one@example.com"/>
              </div>
              <div className='mb-3'>
                
              <Autocomplete
                      disablePortal
                      id="combo-box-demo"
                      options={services}
                      sx={{ width: 230 }}
                      renderInput={(params) => <TextField {...params} label="Select Service" />}
                  />
                </div>
              <div className="mb-3">
                <label for="exampleFormControlTextarea1" class="form-label">Place project's remark's here</label>
                <textarea className="form-control" id="exampleFormControlTextarea1" rows="3"></textarea>
             
              </div>
              <div className='mb-3'>
            
                
                     <button className='btn btn-dark my-2'>Send Feedback</button>

              
              </div>  
             
                
            </div>
            
            
            
          </div>
            
                   
                    
        </div>
            


      </>
        
  );
}

